header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
}

header div h1 {
    text-align: center;
}

header div h3 {
    text-align: center;
}

a div {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--text-color);
}

.links {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}